export default {
    data() {
        return {
            spaces: [],
        };
    },
    created() {
        this.loadData();
    },
    methods: {
        loadData() {
            let data = [];
            let data_list = [];
            for (let i = 0; i < data.length; i++) {
                data_list.push(data[i]);
                if (data_list.length === 6 || i === data.length - 1) {
                    this.spaces.push(data_list);
                    data_list = [];
                }
            }
        },
        errorHandler() {
            return true
        }
    },
}
